.question {
  background-color: #fefefe;
  border-radius: 8px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.04);
  padding: 14px;

  //   toda .question que for seguida da .question (&) vai adicionar esse margin, significa esse margin vai adicionar a partir da 2Â° em diante
  & + .question {
    margin-top: 8px;
  }

  &.highlighted {
    background-color: #f4f0ff;
    border: 1px solid #835afd;

    footer .userInfo span {
      color: #29292e;
    }
  }

  &.answered {
    background-color: #dbdcdd;
  }

  p {
    color: #29292e;
  }

  footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;

    .userInfo {
      display: flex;
      align-items: center;

      img {
        width: 32px;
        height: 32px;
        border-radius: 50%;
      }

      > span {
        margin-left: 8px;
        color: #737380;
        font-size: 14px;
      }
    }

    > div {
      display: flex;
      gap: 16px;
    }

    button {
      border: 0;
      background-color: transparent;
      cursor: pointer;
      transition: filter 0.2s;

      &.likeButton {
        display: flex;
        align-items: flex-end;
        color: #737380;
        gap: 8px;

        &.liked {
          color: #835afd;

          svg path {
            stroke: #835afd;
          }
        }
      }

      &.hover {
        filter: brightness(0.7);
      }
    }
  }
}
